/* Global Page Styles for Dark Mode */

body {
    background-color: #282c34; /* Dark background for the whole page */
    color: #fff; /* Light text color for better readability */
    font-family: Arial, sans-serif; /* Optional: set a default font */
}

/* AssetDateInput.css - Dark Mode Style */

.date-input-table {
    margin: 0 auto;
    border-collapse: collapse;
    width: 80%;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    text-align: center;
    background-color: #333; /* Dark background for the table */
    color: #fff; /* Light text color for table */
}

.date-input-table tr:nth-child(odd) {
    background-color: #424242; /* Slightly lighter for alternate rows */
}

.date-input-table th, .date-input-table td {
    padding: 8px;
}

.date-input-table th {
    background-color: #555; /* Darker background for headers */
}

.date-input-table td {
    border-bottom: 1px solid #666; /* Dark border for cells */
}

.date-indicator {
    display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-right: 5px;
    vertical-align: middle;
}

.date-group label {
    margin-left: 10px;
    color: #ccc; /* Lighter text color for labels */
}

.date-group input[type="date"] {
    border: 1px solid #777; /* Dark border for inputs */
    border-radius: 4px;
    padding: 4px;
    background-color: #555; /* Dark background for inputs */
    color: #fff; /* Light text color for inputs */
}

.date-group input[type="date"]:focus {
    border-color: #4CAF50;
}

.button-container {
    text-align: center;
    margin-top: 20px;
}
